import moment from 'moment';

class PointBalance extends Component {
	render() {
		return (
			<div className="pointsBalance">
				{this.props.points} points<br />
				Matthias Prilipp
			</div>
		);
	}
}

class MenuButton extends Component {
	render() {
		var className = 'menuButton';

		if (this.props.iconText === this.props.selected) className += ' menuButtonSelected';

		return (
			<div className={className} onClick={this.props.onClick}>
				<FontAwesomeIcon icon={this.props.icon} className="menuButtonIcon" onClick={this.props.onClick} />
				<br />
				{this.props.iconText}
			</div>
		);
	}
}

